.layout-root {
    height: 100%;
    display: flex;
    flex-direction: column;

    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: stretch;
    align-content: stretch;
}

header {
    width: 100%;
    height: 60px;
    background-color: #101010;
    display: flex;
    align-items: center;
}

.layout-container {
    flex: 1;
    display: flex;
    flex-direction: column;
}

header>.logo {
    margin-left: 30px;
    width: 26px;
    height: 17px;
    background-image: url("./../../images/logo.svg");
}

header>.better-meter {
    margin-left: 10px;
    width: 137px;
    height: 18px;
    background-image: url("./../../images/better_meter.svg");
}

header>.header-controls {
    margin-left: auto;
    height: 100%;
    display: flex;
}

header>.header-controls>* {
    height: 100%;
    border-left: 1px solid #333333;
}

header>.header-controls>.search,
header>.header-controls>.notifications {
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}

header>.header-controls>.notifications>.notifications-icon {
    width: 18px;
    height: 18px;
    background-image: url("./../../images/icons/bell.svg");
    margin-left: -10px;
}

header>.header-controls>.notifications>.MuiBadge-root {
    left: 10px;
}

header>.header-controls>.notifications>.MuiBadge-root>.MuiBadge-badge {
    background-color: #FF5C00;
    color: #F6F6F6;
    font-family: DM Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: center;
}

header>.header-controls>.search>.search-icon {
    width: 18px;
    height: 18px;
    background-image: url("./../../images/icons/search.svg");
}

header>.header-controls>.user {

    display: flex;
    align-items: center;
}

header>.header-controls>.user>.avatar {
    margin-left: 15px;
    margin-right: 15px;
    width: 36px;
    height: 36px;
    background-image: url("./../../images/avatar.png");
    border-radius: 100%;
}

header>.header-controls>.user>.username {
    font-family: DM Sans;
    font-size: 17px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0em;
    color: #ffffff;
    margin-right: 10px;
}

header>.header-controls>.user>.choose-user-icon {
    width: 14px;
    height: 8px;
    background-image: url("./../../images/icons/dropdown-arrow.svg");
    margin-right: 25px;
}

footer {
    font-family: IBM Plex Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 14px;
    letter-spacing: 0em;
    text-align: right;

    color: #5D6677;
    background-color: #F8F5F5;
    padding-top: 140px;
    padding-bottom: 30px;
    padding-right: 80px;
}