.dashboard-container {
    flex: 1;
    background-color: #F8F5F5;
    display: flex;
    flex-direction: column;
}

.dashboard-container>.inner-container {
    padding-left: 80px;
    padding-right: 80px;
    padding-top: 70px;
    flex: 1;
    display: flex;
    flex-direction: column;
}

.dashboard-container>.inner-container>.dashboard-header {
    display: flex;
    flex-direction: row;
}

.dashboard-container>.inner-container>.dashboard-header>h1 {
    font-family: DM Sans;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: left;

    display: inline-flex;
    margin: 0;
}

.dashboard-container>.inner-container>.dashboard-header>.filters {
    margin-left: auto;
}

.dashboard-container>.inner-container>.dashboard-header>.filters>.sites-container {
    width: 200px;
    margin-right: 50px;
}

/* TODO: Styling for custom selects doesn't work */
/* .dashboard-container>.inner-container>.dashboard-header>.filters>.sites-container .MuiMenu-list
{
    background-color: #0C172E;
} */

.dashboard-container>.inner-container>.dashboard-header>.filters>.sites-container .MuiOutlinedInput-notchedOutline {
    border-color: rgba(0, 0, 0, 0.23);
}

.dashboard-container>.inner-container>.dashboard-header>.filters>.sites-container .MuiFormLabel-root.Mui-focused {
    color: rgba(0, 0, 0, 0.23);
}

.dashboard-container>.inner-container>.dashboard-header>.filters>.events-container {
    width: 200px;
    margin-right: 50px;
}

.dashboard-container>.inner-container>.dashboard-header>.filters>.events-container .MuiOutlinedInput-notchedOutline {
    border-color: rgba(0, 0, 0, 0.23);
}

.dashboard-container>.inner-container>.dashboard-header>.filters>.events-container .MuiFormLabel-root.Mui-focused {
    color: rgba(0, 0, 0, 0.23);
}

.dashboard-container>.inner-container>.dashboard-paper {
    flex: 1;
    margin-top: 80px;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs.MuiTabs-root {
    background-color: #F8F5F5;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .MuiTab-root {
    background-color: #FCFAFA;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .MuiTab-root.Mui-selected {
    background-color: #FFFFFF;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .MuiTabs-indicator {
    display: none;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .MuiTab-root:first-child {
    border-radius: 5px 5px 0 0;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .MuiTab-root:last-child {
    border-radius: 0 5px 0 0;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .tab-header>.tab-title {
    font-family: DM Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 12px;
    letter-spacing: 0em;
    text-align: center;

    text-transform: none;
    color: #84868A;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .tab-header>.tab-content {
    display: flex;
    flex-direction: row;
    margin-top: 5px;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .tab-header>.tab-content>.tab-icon {
    width: 24px;
    height: 14px;
    background-image: url("./../../../images/icons/graph.svg");
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .tab-header>.tab-content>.tab-amount {
    font-family: DM Mono;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: left;

    text-transform: none;
    margin-left: 10px;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-tabs .tab-header>.tab-content>.tab-change {
    font-family: DM Mono;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: center;

    color: #01653B;
    background-color: #BEEDC0;
    margin-left: 10px;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 7px;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-content {
    border-radius: 0 20px 20px 20px;
    display: flex;
    flex-direction: row;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-content>.dashboard-pageviews {
    width: 30%;
    margin-left: 40px;
    margin-right: 40px;
    margin-top: 40px;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-content>.dashboard-graph {
    width: 1177px;
    height: 496px;
    background-image: url("./../../../images/graph.svg");

    margin-left: 40px;
    margin-right: 40px;
    margin-top: 40px;
    margin-bottom: 40px;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-content>.dashboard-pageviews>ul {
    padding-inline-start: 0;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-content>.dashboard-pageviews>ul>li {
    list-style: none;

    display: flex;
    flex-direction: row;

    border-bottom: 1px solid #E5E5E5;

    padding-top: 10px;
    padding-bottom: 10px;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-content>.dashboard-pageviews>ul>li>.pageview-icon {
    width: 30px;
    height: 30px;

    background-color: #FF9900;
    border-radius: 50%;

    font-family: DM Sans;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: center;

    color: #FFFFFF;
}

.dashboard-container>.inner-container>.dashboard-paper>.dashboard-content>.dashboard-pageviews>ul>li>.pageview-amount {
    margin-left: auto;

    font-family: DM Mono;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: right;
}


.dashboard-container>.inner-container>.cards-container>.bottom-card>h2 {
    font-family: DM Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
}

.dashboard-container>.inner-container>.cards-container>.bottom-card>ul {
    padding-inline-start: 0;
}

.dashboard-container>.inner-container>.cards-container>.bottom-card>ul>li {
    list-style: none;

    display: flex;
    flex-direction: row;

    border-bottom: 1px solid #E5E5E5;

    padding-top: 10px;
    padding-bottom: 10px;
}

.dashboard-container>.inner-container>.cards-container>.bottom-card>ul>li>.bottom-card-icon {
    width: 30px;
    height: 30px;

    background-color: #127BF5;
    border-radius: 50%;

    font-family: DM Sans;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: center;

    color: #FFFFFF;
}

.dashboard-container>.inner-container>.cards-container>.bottom-card>ul>li>.bottom-card-name {
    margin-left: 20px;

    font-family: DM Sans;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
}

.dashboard-container>.inner-container>.cards-container>.bottom-card>ul>li>.bottom-card-amount {
    margin-left: auto;

    font-family: DM Mono;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: right;
}

.dashboard-container>.inner-container>.cards-container {
    display: flex;
    flex-direction: row;
    margin-top: 40px;
}

.dashboard-container>.inner-container>.cards-container>.bottom-card {
    width: 445px;
    margin-right: 40px;

    padding-left: 20px;
    padding-right: 20px;
}

.dashboard-container>.inner-container>.cards-container>.add-new-card {
    width: 305px;

    background-color: #FCFAFA;
}