.main-menu .dashboard-icon {
    width: 18px;
    height: 19px;
    background-image: url("./../../../images/icons/dashboard.svg");

    margin-right: 15px;
    margin-bottom: 0 !important;
}

.main-menu .MuiTab-wrapper {
    flex-direction: row;

    font-family: DM Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
    letter-spacing: 0em;
    text-align: left;
    color: #1A2437;
}

.main-menu .MuiTabs-indicator
{
    display: none;
}

.main-menu .Mui-selected
{
    background-color: #F8F5F5;
}

.main-menu .MuiButtonBase-root
{
    border-right: 1px solid #E5E5E5;
    text-transform: none;
}